Vue.use(Router)


const router =  new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component:index,
      meta:{
        requireAuth: true
      },

    },{
      path: '/loginhome',
      name: 'loginhome',
      component:loginhome,
      meta: {
        keepAlive: true,
        requireAuth: true,
      },

    },{
      path: '/logincode',
      name: 'logincode',
      component: logincode,
      meta:{
        keepAlive: true,
        requireAuth: true,
      },

    },{
      path: '/register',
      name: 'register',
      component: register
    },{
      path: '/activity',
      name: 'activity',
      component: activity,
      meta:{
        requireAuth: true
      },

    },{
      path: '/culture',
      name: 'culture',
      component: culture,
      meta:{
        requireAuth: true
      },

    },{
      path: '/my',
      name: 'my',
      component: my,

    },{
      path: '/activityrecord',
      name: 'activityrecord',
      component: activityrecord
    },{
      path: '/customer',
      name: 'customer',
      component: customer
    },{
      path: '/activeshare/:post_id',
      name: 'activeshare',
      component: activeshare,

    },{
      path: '/classs',
      name: 'classs',
      component:classs,
      meta: {
        keepAlive: true,
      }
    },{
      path: '/class_details',
      name: 'class_details',
      component:class_details
    },{
      path: '/honor',
      name: 'honor',
      component:honor
    },{
      path: '/honor_details',
      name: 'honor_details',
      component:honor_details
    },{
      path: '/pace',
      name: 'pace',
      component:pace
    },{
      path: '/club',
      name: 'club',
      component:club
    },{
      path: '/group',
      name: 'group',
      component:group,
    },{
      path: '/activity2',
      name: 'activity2',
      component:activity2,
    },
    {
      path: '/shezhi',
      name: 'shezhi',
      component:shezhi
    },
    {
      path: '/contactus',
      name: 'contactus',
      component:contactus
    },
    {
      path: '/myinfo',
      name: 'myinfo',
      component:myinfo
    },
    {
      path: '/shezhixy',
      name: 'shezhixy',
      component:shezhixy
    },
    {
      path: '/shezhizc',
      name: 'shezhizc',
      component:shezhizc
    },{
      path: '/aydetails',
      name: 'aydetails',
      component:aydetails
    },{
      path: '/author',
      name: 'author',
      component: author,
      // meta: {keepAlive: true}
    },

  ]
})


//




const baseurl = "http://www.staffhome.cn";//本地测试地址
// const baseurl = "http://192.168.31.185:8080/";//正式地址

// const baseurl = "http://unionfit.zongyutech.com";//正式地址
// 请求微信服务器获取code 方法
function getUserCode() {
  let redirectURL = encodeURI((baseurl+"/author").split('&code')[0]);//获取地址
  const base = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=';
  let state;
  let surl;
  let reserveUrl;
  let appid = "wx49dc28f308a61452";
  let scope = "snsapi_userinfo";
  // alert(redirectURL)
  // return
window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirectURL}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`
  // window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1c24d0b2f06df7f9&redirect_uri=http%3A%2F%2Ftest.sports.zongyutech.com%2Fapi%2Flogin%2Ftest&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect';
}


router.beforeEach((to, from, next) => {
  console.log(to)
  console.log(from)
  console.log("token上边")
  let tokenid = localStorage.getItem("token");
  let companyid = localStorage.getItem("company_id");
  let code = localStorage.getItem("code");

  const agent = navigator.userAgent
  const isiOS = !!agent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端

  if (!to.meta.requireAuth) {
      if (isiOS && to.path !== location.pathname) {
          // 此处不可使用location.replace
          location.assign(to.fullPath)
        } else {
          next()
        }
  } else {

    // localStorage.getItem("code")==null||window.localStorage.getItem("code")===undefined||window.localStorage.getItem("code")=='')
      if ( localStorage.getItem("code")=="undefined" || localStorage.getItem("code")=="" || localStorage.getItem("code")==null || localStorage.getItem("code")==undefined) {
          console.log("请求微信服务器获取code");
          // localStorage.removeItem('code')
          // alert("没有code!!");
          getUserCode();

       }
       else {

          if (isiOS && to.path !== location.pathname) {
              // 此处不可使用location.replace
                  location.assign(to.fullPath)
          }
          else if (!companyid){
             // alert("没有compyandid")
             if (to.path === '/loginhome') {
               next();
             }else{
               // alert("没有token")
               next({
                 path:"/loginhome"
               });
             }
           }
           else if (!tokenid){

                 if (to.path === '/logincode') {
                   next();
                 }else{
                   // alert("没有token")
                   next({
                     path:"/logincode"
                   });
                 }

           }
           else {
              next();
            }
        }

    }


})




export default router



